.mainBanner{width:100vw;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.mainBanner .carousel{width:100%;height:100%}.mainBanner .carousel-inner{width:inherit;height:inherit}.mainBanner .carousel-control-prev,.mainBanner .carousel-control-next{min-height:268px;height:42vh}.mainBanner .carousel-indicators{top:91%}.mainBanner .carousel-indicators li{background-color:#161616}@media (min-width: 48em){.mainBanner{height:72vh;min-height:400px}.mainBanner .carousel-control-prev,.mainBanner .carousel-control-next{min-height:350px;height:63vh}}.mainSlideBanner{width:100%;height:100%;padding-bottom:3rem}.mainSlideBanner__boxImg{height:42vh;min-height:268px;background-size:cover;background-repeat:no-repeat;background-position:center}.mainSlideBanner__txtBox{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding:1rem 1.5rem}.mainSlideBanner__txtBox .title{margin:0;font-size:1.5rem;font-weight:700;line-height:1.5rem}.mainSlideBanner__txtBox .paragraph{font-size:0.9rem;line-height:1.1rem;margin:.2rem 0 .5rem 0}@media (min-width: 48em){.mainSlideBanner__boxImg{height:63vh;min-height:350px}.mainSlideBanner__txtBox{position:absolute;bottom:12%;max-height:42%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding:.8rem .8rem 1.2rem;min-width:48vw;max-width:51vw;box-shadow:0 0 0 200px rgba(255,255,255,0.5) inset}.mainSlideBanner__txtBox .line-clamp{display:-webkit-box;-webkit-line-clamp:3;-webkit-box-orient:vertical;overflow:hidden;padding-bottom:0.2rem}}

