.follow-me{font-size:1.2rem;font-weight:700;line-height:1.3;width:100%;margin-bottom:0;text-align:center}.follow-me a{color:black}.instagram-feed{margin:1rem auto;display:grid;grid-template-columns:repeat(2, 1fr)}.instagram-feed .post{min-width:160px;background-size:cover;background-position:center center;background-repeat:no-repeat}.instagram-feed .post:after{content:"";display:block;padding-bottom:100%}.instagram-feed .post:nth-last-child(-n+2){display:none}@media (min-width: 30em){.instagram-feed{grid-template-columns:repeat(3, 1fr)}.instagram-feed .post:nth-last-child(2){display:block}}@media (min-width: 48em){.instagram-feed{grid-template-columns:repeat(4, 1fr)}.instagram-feed .post{min-width:170px}.instagram-feed .post:nth-last-child(3){display:none}}@media (min-width: 64em){.instagram-feed{grid-template-columns:repeat(5, 1fr)}.instagram-feed .post:nth-last-child(-n+3){display:block}}

.footer{grid-row:15 / auto}.footer__border{width:100vw}.footer__border span{display:inline-block;width:100%;overflow:hidden;white-space:nowrap;font-size:2rem;line-height:0.5;color:#AC90F1}.footer .top-border{font-family:Spot-Matrix}.footer .top-border span{font-size:4rem}.footer .middle-border{font-family:Embroidery}.footer__input-wrapper{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;width:87%;margin:0 auto 2rem}.footer__input-wrapper p{text-align:center;font-size:0.9rem}.footer__input-wrapper .custom-input{width:100%;border-radius:5%;margin:0 auto}.footer__input-wrapper .form-control{border-radius:35px 0 0 35px;border-color:#AC90F1;border-right:none;font-size:0.9rem}.footer__input-wrapper .btn{color:black;line-height:1;background:transparent;border-color:#AC90F1;border-left:none;border-radius:0 35px 35px 0 !important;font-size:0.9rem;font-weight:600}.footer__menu{display:flex;flex-wrap:wrap;justify-content:space-between;width:95%;margin:0.5em auto}.footer__menu ul{list-style:none}.footer__menu ul li{margin-bottom:0.3em;cursor:pointer}.footer__menu ul a{color:#43026A}.footer__menu img{width:2.3em;margin-right:0.8em;cursor:pointer}.footer__copyrights{background:#43026A;height:8vh;min-height:50px;display:flex;justify-content:center;align-items:center}.footer__copyrights .date{color:white;font-size:0.8rem}@media (min-width: 38em){.footer__input-wrapper .custom-input{width:85%}}@media (min-width: 48em){.footer__input-wrapper .custom-input{width:55%}.footer__menu{justify-content:center;margin:1.2em auto}.footer__menu ul{width:100%;display:flex;justify-content:center;padding:0 5%}.footer__menu ul li{display:flex;align-items:center}.footer__menu ul li::after{content:'\25C6';color:#43026A;margin:0 1em}.footer__menu ul li:last-child:after{content:'';margin:0}.footer__menu img{margin:0 0.3em}}@media (min-width: 62em){.footer__input-wrapper .custom-input{width:41%}}

.header{background-size:contain;background-position:center center;height:25vh;min-height:162px;max-height:185px;width:100vw;display:flex;flex-direction:column;justify-content:center;color:white}.header h1{font-size:1.5rem;font-weight:700;text-align:center;text-shadow:2px 2px 2px #161616}.header span{display:inline-block;padding:0 2rem;text-align:center;font-size:1.1rem;line-height:1;text-shadow:2px 2px 2px #161616}@media (min-width: 48em){.header{align-items:flex-start;padding:0 3rem}.header span{padding:0}}

